"use client"

import type React from "react"
import { useState } from "react"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { Search, TrendingUp, Globe, Monitor, Link2 } from "lucide-react"
import { useToast } from "@/hooks/use-toast"
import { StatsCharts } from "@/components/stats-charts"
import { StatsTable } from "@/components/stats-table"

interface URLStats {
  code: string
  longUrl: string
  totalClicks: number
  clicksByDate: { date: string; clicks: number }[]
  clicksByCountry: { country: string; clicks: number }[]
  clicksByDevice: { device: string; clicks: number }[]
  clicksByReferrer: { referrer: string; clicks: number }[]
  createdAt: string
  lastClickAt: string | null
}

export default function StatsPage() {
  const [urlCode, setUrlCode] = useState("")
  const [stats, setStats] = useState<URLStats | null>(null)
  const [isLoading, setIsLoading] = useState(false)
  const { toast } = useToast()

  const handleSearch = async (e: React.FormEvent) => {
    e.preventDefault()

    if (!urlCode) {
      toast({
        title: "Error",
        description: "Please enter a URL code",
        variant: "destructive",
      })
      return
    }

    setIsLoading(true)

    try {
      const response = await fetch(`/api/stats/${urlCode}`)

      if (!response.ok) {
        throw new Error("Failed to fetch stats")
      }

      const data = await response.json()
      setStats(data)
    } catch (error) {
      toast({
        title: "Error",
        description: "Failed to fetch statistics. Please check the URL code.",
        variant: "destructive",
      })
      setStats(null)
    } finally {
      setIsLoading(false)
    }
  }

  const handleReset = () => {
    setUrlCode("")
    setStats(null)
  }

  return (
    <div className="min-h-screen bg-background">
      {/* Header */}
      <header className="border-b border-border bg-card">
        <div className="container mx-auto px-4 py-4">
          <div className="flex items-center gap-2">
            <TrendingUp className="h-6 w-6 text-primary" />
            <span className="text-xl font-semibold">URL Analytics Dashboard</span>
          </div>
        </div>
      </header>

      <div className="container mx-auto px-4 py-8">
        {/* Search Section */}
        <div className="mb-8">
          <Card>
            <CardHeader>
              <CardTitle>Search URL Statistics</CardTitle>
              <CardDescription>Enter a short URL code to view detailed analytics</CardDescription>
            </CardHeader>
            <CardContent>
              <form onSubmit={handleSearch} className="flex gap-3">
                <div className="flex-1 relative">
                  <Search className="absolute left-3 top-1/2 -translate-y-1/2 h-5 w-5 text-muted-foreground" />
                  <Input
                    placeholder="Enter URL code (e.g., abc123)"
                    value={urlCode}
                    onChange={(e) => setUrlCode(e.target.value)}
                    disabled={isLoading}
                    className="pl-10 h-11"
                  />
                </div>
                <Button type="submit" disabled={isLoading} size="lg">
                  {isLoading ? "Searching..." : "Search"}
                </Button>
                {stats && (
                  <Button type="button" variant="outline" onClick={handleReset} size="lg">
                    Clear
                  </Button>
                )}
              </form>
            </CardContent>
          </Card>
        </div>

        {/* Stats Display */}
        {stats && (
          <div className="space-y-6">
            {/* Overview Cards */}
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
              <Card>
                <CardHeader className="pb-3">
                  <CardDescription>Total Clicks</CardDescription>
                </CardHeader>
                <CardContent>
                  <div className="text-3xl font-bold text-primary">{stats.totalClicks.toLocaleString()}</div>
                </CardContent>
              </Card>

              <Card>
                <CardHeader className="pb-3">
                  <CardDescription>Short Code</CardDescription>
                </CardHeader>
                <CardContent>
                  <div className="text-2xl font-mono font-semibold">{stats.code}</div>
                </CardContent>
              </Card>

              <Card>
                <CardHeader className="pb-3">
                  <CardDescription>Created</CardDescription>
                </CardHeader>
                <CardContent>
                  <div className="text-lg font-medium">{new Date(stats.createdAt).toLocaleDateString()}</div>
                </CardContent>
              </Card>

              <Card>
                <CardHeader className="pb-3">
                  <CardDescription>Last Click</CardDescription>
                </CardHeader>
                <CardContent>
                  <div className="text-lg font-medium">
                    {stats.lastClickAt ? new Date(stats.lastClickAt).toLocaleDateString() : "Never"}
                  </div>
                </CardContent>
              </Card>
            </div>

            {/* Original URL */}
            <Card>
              <CardHeader>
                <CardTitle className="text-base flex items-center gap-2">
                  <Link2 className="h-4 w-4" />
                  Original URL
                </CardTitle>
              </CardHeader>
              <CardContent>
                <a
                  href={stats.longUrl}
                  target="_blank"
                  rel="noopener noreferrer"
                  className="text-primary hover:underline break-all"
                >
                  {stats.longUrl}
                </a>
              </CardContent>
            </Card>

            {/* Charts Section */}
            <Tabs defaultValue="timeline" className="space-y-4">
              <TabsList className="grid w-full grid-cols-4">
                <TabsTrigger value="timeline">Timeline</TabsTrigger>
                <TabsTrigger value="geography">Geography</TabsTrigger>
                <TabsTrigger value="devices">Devices</TabsTrigger>
                <TabsTrigger value="referrers">Referrers</TabsTrigger>
              </TabsList>

              <TabsContent value="timeline" className="space-y-4">
                <StatsCharts
                  data={stats.clicksByDate}
                  type="timeline"
                  title="Clicks Over Time"
                  description="Daily click activity for this URL"
                />
              </TabsContent>

              <TabsContent value="geography" className="space-y-4">
                <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
                  <StatsCharts
                    data={stats.clicksByCountry}
                    type="bar"
                    title="Clicks by Country"
                    description="Geographic distribution of clicks"
                  />
                  <Card>
                    <CardHeader>
                      <CardTitle className="flex items-center gap-2">
                        <Globe className="h-5 w-5" />
                        Top Countries
                      </CardTitle>
                    </CardHeader>
                    <CardContent>
                      <StatsTable data={stats.clicksByCountry} type="country" />
                    </CardContent>
                  </Card>
                </div>
              </TabsContent>

              <TabsContent value="devices" className="space-y-4">
                <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
                  <StatsCharts
                    data={stats.clicksByDevice}
                    type="pie"
                    title="Device Types"
                    description="Breakdown of devices used to access this URL"
                  />
                  <Card>
                    <CardHeader>
                      <CardTitle className="flex items-center gap-2">
                        <Monitor className="h-5 w-5" />
                        Device Details
                      </CardTitle>
                    </CardHeader>
                    <CardContent>
                      <StatsTable data={stats.clicksByDevice} type="device" />
                    </CardContent>
                  </Card>
                </div>
              </TabsContent>

              <TabsContent value="referrers" className="space-y-4">
                <Card>
                  <CardHeader>
                    <CardTitle>Traffic Sources</CardTitle>
                    <CardDescription>Where your clicks are coming from</CardDescription>
                  </CardHeader>
                  <CardContent>
                    <StatsTable data={stats.clicksByReferrer} type="referrer" />
                  </CardContent>
                </Card>
              </TabsContent>
            </Tabs>
          </div>
        )}

        {/* Empty State */}
        {!stats && !isLoading && (
          <Card className="mt-12">
            <CardContent className="flex flex-col items-center justify-center py-16">
              <TrendingUp className="h-16 w-16 text-muted-foreground mb-4" />
              <h3 className="text-xl font-semibold mb-2">No statistics to display</h3>
              <p className="text-muted-foreground text-center max-w-md">
                Enter a URL code above to view detailed analytics including clicks, geographic data, device information,
                and more.
              </p>
            </CardContent>
          </Card>
        )}
      </div>
    </div>
  )
}
